import { Component, OnInit, Input } from '@angular/core';
import { Task } from '../Task';
import { User } from '../User';
import { TaskService } from '../services/task.service';
import { AuthService } from '../services/auth.service';
import { AngularFireDatabase } from '@angular/fire/database';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-explore-container',
  templateUrl: './explore-container.component.html',
  styleUrls: ['./explore-container.component.scss'],
})
export class ExploreContainerComponent implements OnInit {

  title = 'To-do list';
  description = 'Todo';

  itemValue = '';
  items: Observable<any[]>;

  @Input() name: string;

  tasks: Task[];
  users: User[];
  id: number;
  taskName: string;
  userName: string;

  taskNameSearching: string;
  userNameSearching: string;
  dateSearching: string;

  newTask(): void {
    if (this.taskName != undefined && this.taskName != "" && this.userName != undefined && this.userName != "") {
      let task: Task = { name: this.taskName, date: new Date, user: this.userName };
      this.taskService.pushTask(task);
    }
    else
      alert("Por favor ingresa un usuario o nombre de la tarea");
  }

  getTasks(): void {
    this.tasks = this.taskService.getTasks();
  }

  deleteTask(currentTask: Task): void {

    this.taskService.deleteTask(currentTask);
  }


  selectedTask: Task;
  onSelect(task: Task): void {
    this.selectedTask = task;
  }

  constructor(public db: AngularFireDatabase, private taskService: TaskService, private authSvc: AuthService) {
    this.items = db.list('items').valueChanges();
  }

  ngOnInit() {
    this.getTasks();
    this.getUsers();
  }

  async googleLogin() {
    try {
      const user = await this.authSvc.loginGoogle();
      this.authSvc.pushUser(user.displayName, user.uid);

    } catch (error) {
      console.log(error);
    }
  }

  getUsers(): void {
    this.users = this.authSvc.getUsers();
  }
}
