import { Injectable } from '@angular/core';
import { Tasks } from '../mock-data';
import { Task } from '../Task';

@Injectable({
    providedIn: 'root'
})
export class TaskService {

    getTasks(): Task[] {
        return Tasks;
    }

    pushTask(newPerson: Task): boolean {
        Tasks.push(newPerson);
        return true;
    }

    deleteTask(person: Task): boolean {
        let index = Tasks.indexOf(person);

        if (index != -1)
            Tasks.splice(index, 1);
        return true;
    }

    constructor() { }
}
