import { Injectable } from '@angular/core';
import { User } from '../User';
import { Users } from '../Users';
import { AngularFireAuth } from '@angular/fire/auth';
import firebase from 'firebase/app'
import { Observable, of } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import {
  AngularFirestore,
  AngularFirestoreDocument,
} from '@angular/fire/firestore';

@Injectable({ providedIn: 'root' })
export class AuthService {
  public user$: Observable<User>;

  constructor(public afAuth: AngularFireAuth, private afs: AngularFirestore) {

    this.user$ = this.afAuth.authState.pipe(
      switchMap((user) => {
        if (user) {
          return this.afs.doc<User>(`users/${user.uid}`).valueChanges();
        }
        return of(null);
      })
    );
  }

  async loginGoogle(): Promise<User> {
    try {
      const { user } = await this.afAuth.signInWithPopup(
        new firebase.auth.GoogleAuthProvider()
      );
      this.updateUserData(user);
      return user;
    } catch (error) {
      console.log(error);
    }
  }

  private updateUserData(user: User) {
    const userRef: AngularFirestoreDocument<User> = this.afs.doc(
      `users/${user.uid}`
    );

    const data: User = {
      uid: user.uid,
      displayName: user.displayName,
    };

    return userRef.set(data, { merge: true });
  }

  getUsers(): User[] {
    console.log(Users);
    return Users;
  }

  pushUser(displayName: string, id: string): boolean {
    console.log("GUARDANDO" + displayName);
    const data: User = {
      uid: id,
      displayName: this.createUserName(displayName),
    };
    Users.push(data);
    return true;
  }

  createUserName(displayName: string): string {

    if (displayName.split(' ').length >= 2) {
      let randomNumber = Math.floor(Math.random() * (9999 - 1000) + 1000);
      let newName = "";
      newName = displayName.split(' ')[0].substring(0, 2) + displayName.split(' ')[1].substring(0, 2) + randomNumber;
      return newName;
    }
    return displayName;
  }

}